<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Withdraw</p>
        </div>
        <div class="orderDetailWrap" v-if="withdrawData && withdrawData.status == 'normal'">
            <div class="balanceTopBox">Current Balance: {{moneySymbol+withdrawData.normal.balance}}</div>
            <div class="mainWhiteBox">
                <div class="cashMainBox">
                    <p class="willReceive">Will receive</p>
                    <p class="willReceiveNumber">{{moneySymbol+willReceive}}</p>
                </div>
                <p class="rechargeAmount" style="margin-bottom:.18rem;">Withdraw amount</p>
                <div class="inputWrapBox">
                    <div class="subscribeInputBox flexBetween">
                        <span class='moneySymbol'>{{moneySymbol}}</span>
                        <input class='subscribeInput' type="text" v-model="inputMoney"  @input="checkInput(inputMoney)">
                    </div>
                    <div class="rechargeAlertBox">
                        <p>Your current level is <span style="color:#38ADFF;">{{withdrawData.normal.level.name}}</span>, and the single withdrawal limit is <span style="color:#38ADFF;">{{moneySymbol+selectWayMsg.range.min}}</span> to <span style="color:#38ADFF;">{{moneySymbol+selectWayMsg.range.max}}</span>, <span style="color:#38ADFF;">{{withdrawData.normal.level.withdrawLeft/1 + withdrawData.normal.level.withdrawDone/1}}</span> times a day.</p>
                        <p>You have <span :style="withdrawData.normal.level.withdrawLeft != 0 ? 'color:#38ADFF;': 'colro:#FF305F;'">{{withdrawData.normal.level.withdrawLeft}}</span> withdrawals left today.</p>
                        <p v-if="selectWayMsg && selectWayMsg.fee && (selectWayMsg.fee.rate/1 > 0 || selectWayMsg.fee.fix/1 > 0)">Fee: <span v-if="selectWayMsg.fee.rate/1 > 0">{{parseFloat((selectWayMsg.fee.rate/1*100).toFixed(2))}}% when ≥ {{moneySymbol+selectWayMsg.fee.condition}}</span> <span v-if="selectWayMsg.fee.rate/1 > 0 && selectWayMsg.fee.fix/1 > 0">and</span> <span v-if="selectWayMsg.fee.fix/1 > 0">{{moneySymbol+selectWayMsg.fee.fix}} when &#60; {{moneySymbol+selectWayMsg.fee.condition}}</span> </p>
                    </div>
                </div>
                <p class="rechargeAmount" style="margin-top: .36rem;margin-bottom: .26rem;">Withdraw to</p>
                <div class="withdrawWayBox">
                    <div class="withdrawWayItem flexBetween" :class='selectWayName == item.type ? "selectWayActive":""' v-for="(item,index) in withdrawData.normal.support" :key="index" @click="selectWay(item)">
                        <template v-if="item.type == 'IFSC'">
                            <div class="itemLeft flexStart">
                                <img class='videoSelectIcon' src="../assets/images/videoSelectIcon.png" alt="" v-if="selectWayName == item.type">
                                <img class='videoSelectIcon' src="../assets/images/videoNoSelectIcon.png" alt="" v-else>
                                <img class='bankCardIcon' src="../assets/images/bankCardIcon.png" alt="" >
                                <div class="wayMsg">
                                    <p class="wayName">{{item.name}}
                                        <span class='RecommendedText' v-if="item.recommended">Recommended</span>
                                    </p>
                                    <p class="wayNumber">{{settingsData.IFSC.account ? settingsData.IFSC.account : 'No account linked'}}</p>
                                </div>
                            </div>
                            <p @click.stop="goPage('/WithdrawalSettings')" class="editWay">{{settingsData.IFSC.account ? 'Edit' : 'Add'}}</p>
                        </template>
                    </div>
                </div>
                <div class="submitBtn" :class='willReceive != "N/A" && withdrawData.normal.level.withdrawLeft/1 > 0  ? "GradientAllowBtn":""' style="margin-top: .39rem; margin-bottom: .27rem;" @click="submitWithdraw()">Submit</div>
                <p class="withdrawScript">{{withdrawData.normal.workingTime}}</p>
                <p class="withdrawScript">All withdrawals will arrive on the next working day after the application is approved.</p>
            </div>
        </div>
        <div class="orderDetailWrap" v-else-if="withdrawData && withdrawData.status == 'wait'">
            <div class="withdrawWhiteBox">
                <div class="statusTopBox flexCenter">
                    <img class='withdrawStatusIcon' src="../assets/images/withdrawWait.png" alt="">
                    <p class="statusAlertText">Your application has been submitted<br>Please wait for processing</p>
                </div>
                <div class="statusMsgBox">
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Withdraw to:</p>
                        <p class="msgItemRight">{{withdrawData.wait.type}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Amount:</p>
                        <p class="msgItemRight">{{moneySymbol+withdrawData.wait.amount}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Will receive:</p>
                        <p class="msgItemRight">{{moneySymbol+withdrawData.wait.receive}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Submitted on:</p>
                        <p class="msgItemRight">{{withdrawData.wait.time}}</p>
                    </div>
                </div>
                <div class="submitBtn blueBorderBtn" style="margin-top: .54rem;" @click="alertModelBox = true">Cancel Application</div>
            </div>
        </div>
        <div class="orderDetailWrap" v-else-if="withdrawData && withdrawData.status == 'succ'">
            <div class="withdrawWhiteBox">
                <div class="statusTopBox flexCenter">
                    <img class='withdrawStatusIcon' src="../assets/images/withdrawSuccess.png" alt="">
                    <p class="statusAlertText" style="color:#38ADFF;">Your last application was successful<br>Remittance has been submitted</p>
                </div>
                <div class="statusMsgBox">
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Withdraw to:</p>
                        <p class="msgItemRight">{{withdrawData.succ.type}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Amount:</p>
                        <p class="msgItemRight">{{moneySymbol+withdrawData.succ.amount}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Will receive:</p>
                        <p class="msgItemRight">{{moneySymbol+withdrawData.succ.receive}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Submitted on:</p>
                        <p class="msgItemRight">{{withdrawData.succ.time}}</p>
                    </div>
                </div>
                <div class="submitBtn GradientAllowBtn" style="margin-top: .54rem;" @click="replyNotice(withdrawData.succ.id)">Confirm</div>
            </div>
        </div>
        <div class="orderDetailWrap" v-else-if="withdrawData && withdrawData.status == 'reject'">
            <div class="withdrawWhiteBox">
                <div class="statusTopBox flexCenter">
                    <img class='withdrawStatusIcon' src="../assets/images/withdrawFailed.png" alt="">
                    <p class="statusAlertText" style="color:#FF305F;">Your last application has failed<br>{{withdrawData.reject.reason}}</p>
                </div>
                <div class="statusMsgBox">
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Withdraw to:</p>
                        <p class="msgItemRight">{{withdrawData.reject.type}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Amount:</p>
                        <p class="msgItemRight">{{moneySymbol+withdrawData.reject.amount}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Will receive:</p>
                        <p class="msgItemRight">{{moneySymbol+withdrawData.reject.receive}}</p>
                    </div>
                    <div class="statusMsgItem flexBetween">
                        <p class="msgItemLeft">Submitted on:</p>
                        <p class="msgItemRight">{{withdrawData.reject.time}}</p>
                    </div>
                </div>
                <div class="submitBtn GradientAllowBtn" style="margin-top: .54rem;" @click="replyNotice(withdrawData.reject.id)">Confirm</div>
            </div>
        </div>
        <van-dialog class='alertModelBox' v-model="alertModelBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Note
                    <img class='alertModelCloseIcon' @click="alertModelBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">Are you sure to cancel this withdrawal application?</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 blueAllowBtn" style="width: 2.9rem;" @click="alertModelBox = false">No</div>
                        <div class="submitBtn_91 blueBorderBtn" style="width: 2.9rem;" @click="cancelApplication(withdrawData.wait.id)">Yes</div>
                    </div>
                </div>
            </template>
        </van-dialog>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'Withdraw',
		data: function(){
			return {
                showLoading: true,
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                fixNumber: https.fixNumber,

                payUrl: '',
                pamentBox: false,

                withdrawData: '',
                settingsData: {},
                selectWayName: '',
                selectWayMsg: {},
                alertModelBox: false,
                inputMoney: '',
			}
		},
		created(){
			this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'recharge'
                }
				https.fetchGet('/api/pay/withdraw/status',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.withdrawData = res.data
                        if(this.withdrawData.normal && this.withdrawData.normal.settings && this.withdrawData.normal.settings.length > 0) {
                            let settingsArr = this.withdrawData.normal.settings
                            for (let index = 0; index < settingsArr.length; index++) {
                                if(settingsArr[index].type == 'IFSC')
                                this.settingsData['IFSC'] = settingsArr[index].IFSC
                            }
                            console.log(this.settingsData)
                        }
                        if(this.withdrawData.normal && this.withdrawData.normal.support && this.withdrawData.normal.support.length > 0) {
                            this.selectWayMsg = this.withdrawData.normal.support[0]
                            this.selectWayName = this.withdrawData.normal.support[0].type
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            cancelApplication(id) {
                this.alertModelBox = false
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    id: id
                }
				https.fetchPost('/api/pay/withdraw/cancel',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.getData()
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.cancelApplication()
					}
				})
            },
            replyNotice(id) {
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    id: id
                }
				https.fetchPost('/api/pay/withdraw/reply',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.getData()
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.cancelApplication()
					}
				})
            },
            submitWithdraw(){
                if(this.inputMoney == '') {
                    this.$toast({
                        message: 'Enter the amount',
                        duration: 2000,
                        className: 'smallToast'
                    });
                    return
                }else if(this.inputMoney/1 < this.selectWayMsg.range.min/1 ) {
                    this.$toast({
                        message: 'The amount must be\nmore than '+this.selectWayMsg.range.min,
                        duration: 2000,
                        className: 'smallToast'
                    });
                    return
                }else if(this.inputMoney/1 > this.selectWayMsg.range.max/1 ) {
                    this.$toast({
                        message: 'The amount must be\nless than '+this.selectWayMsg.range.max,
                        duration: 2000,
                        className: 'smallToast'
                    });
                    return
                }else if(this.withdrawData.normal.level.withdrawLeft == 0) {
                    this.$toast({
                        message: 'Try again tomorrow',
                        duration: 2000,
                        className: 'smallToast'
                    });
                    return
                }
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Preparing...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: this.selectWayMsg.type,
                    amount: this.inputMoney
                }
				https.fetchPost('/api/pay/withdraw/submit',params).then((data) => {
					this.$toast.clear()
                    this.showLoading = false
					let res = data.data
					if(res.code == 0) {
                        this.getData()
                        // this.payUrl = res.data.openUrl
                        // this.pamentBox = true

					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: 'Unable to\ninitiate payment',
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
				})
            },
            selectWay(item){
                this.selectWayName = item.type
                this.selectWayMsg = item
            },
            checkInput(inputMoney){
                console.log(inputMoney)
                // let rge = /^[0-9]+$/g;
                // if(!rge.test(inputMoney)) {
                //     this.inputCheckAlert = true
				// 	this.inputCheckText = 'Recharge amount cannot contain decimals.'
                // }else if(inputMoney/1 < this.rechargeData.bank.custom.min/1){
				// 	this.inputCheckAlert = true
				// 	this.inputCheckText = 'Single recharge must ≥'+this.moneySymbol+this.rechargeData.bank.custom.min+'.'
                // }else if(inputMoney/1 > this.rechargeData.bank.custom.max/1){
				// 	this.inputCheckAlert = true
				// 	this.inputCheckText = 'Recharge amount exceeds the limit.'
                // }else {
                //     this.inputCheckAlert = false
				// 	this.inputCheckText = ''
                // }
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed: {
            willReceive(){
                let rge = /^[0-9.]+$/g;
                if(this.inputMoney && rge.test(this.inputMoney)) {
                    if(this.inputMoney/1 < this.selectWayMsg.range.min/1 || this.inputMoney/1 > this.selectWayMsg.range.max/1) {
                        return 'N/A'
                    }else {
                        if(this.inputMoney/1 < this.selectWayMsg.fee.condition/1) {
                            return parseFloat((this.inputMoney/1 - this.selectWayMsg.fee.fix).toFixed(this.fixNumber))
                        }else {
                            return parseFloat((this.inputMoney/1 - ((this.inputMoney/1) * this.selectWayMsg.fee.rate)).toFixed(this.fixNumber))
                        }
                    }
                }else {
                    return '--'
                }
            },
        },
        destroyed() {
            clearInterval(this.timer2)
        }
	}
</script>
